
.scroller[data-v-5b49628f]{
  min-height: 101%;/*修正内容高度不够无法scroll*/
  -webkit-tap-highlight-color:rgba(0,0,0,0);
}
.pulldown[data-v-5b49628f],.pullup[data-v-5b49628f]{
  transition: all 0.2s linear;
}
.hide[data-v-5b49628f]{
  display: none;
}
.pull-container[data-v-5b49628f] {
  overflow: hidden; 
  transform: translateZ(0);
  user-select: none;
  text-size-adjust: none;
}

/*refresh start*/
.pulldown[data-v-5b49628f],.pullup[data-v-5b49628f] {  
  height: 50px;  
  line-height: 50px;  
  padding: 5px 15px;  
  font-weight: bold;  
  font-size: 14px;  
  color: #888;  
  text-align: center;
}
.pulldown .pulldown-icon[data-v-5b49628f],.pullup .pullup-icon[data-v-5b49628f] {  
  display: block;   
  width: 40px;  
  height: 40px;  
  background: url(./pull-icon@2x.png) 0 0 no-repeat;  
  -webkit-background-size: 40px 80px;  
  background-size: 40px 80px;  
  -webkit-transition-property: -webkit-transform;  
  -webkit-transition-duration: 250ms;
}
.pulldown .pulldown-label[data-v-5b49628f], .pullup .pullup-label[data-v-5b49628f]{  
  position:relative;  
  top: -40px;
}
.pulldown .pulldown-icon[data-v-5b49628f] {  
  -webkit-transform: rotate(0deg) translateZ(0);
}
.pullup .pullup-icon[data-v-5b49628f] {  
  -webkit-transform: rotate(-180deg) translateZ(0);
}
.pulldown.release .pulldown-icon[data-v-5b49628f] {  
  -webkit-transform: rotate(-180deg) translateZ(0);
}
.pullup.release .pullup-icon[data-v-5b49628f] {  
  -webkit-transform: rotate(0deg) translateZ(0);
}
.pulldown.refresh .pulldown-icon[data-v-5b49628f],.pullup.refresh .pullup-icon[data-v-5b49628f] {  
  background-position: 0 100%;  
  -webkit-transition-duration: 0ms;  
  -webkit-animation: spinner 3s infinite linear;
  animation: spinner 3s infinite linear;
}
/*通用转动动画*/
@-webkit-keyframes spinner {
0% { -webkit-transform: rotate(0deg);
}
50% { -webkit-transform: rotate(180deg);
}
100% { -webkit-transform: rotate(360deg);
}
}
/*refresh end*/




