
body {
  font-family: Helvetica, sans-serif;
}
.page{
  height: 400px;
  background: #eee;
  position: relative;
  /* Prevent native touch events on Windows */
  -ms-touch-action: none;
  /* Prevent the callout on tap-hold and text selection */
  -webkit-touch-callout: none;
  user-select: none;
  text-size-adjust: none;
}
#app {
  height: 100%;
  max-width: 400px;
  margin: 0 auto
}
ul{
  list-style: none;
  padding: 0px;
  margin: 0px
}
ul>li:nth-child(even){
  background-color: #fff
}

/*scrollbar start*/
.iScrollVerticalScrollbar {
  position: absolute;
  z-index: 9999;
  width: 2px;
  bottom: 2px;
  top: 2px;
  right: 2px;
  overflow: hidden;
}
.iScrollVerticalScrollbar.iScrollBothScrollbars {
  bottom: 18px;
}
.iScrollIndicator {
  position: absolute;
  background: #999;
  border-radius: 6px;
  opacity: .8;
}
.iScrollVerticalScrollbar .iScrollIndicator {
  width: 100%;
  background: #999;
}
/*scrollbar end*/



